// es6
import $ from "jquery"
window.$ = $

import Vue from "vue"
import VueRouter from "vue-router"

// 
Vue.use(VueRouter)

window.mui = require("mui")

// 	+
import index from "./vue/index.vue"
import detail from "./vue/detail.vue"

// vue 
var router = new VueRouter({
	routes: [{
		path: "/index",
		component: index
	}, {
		path: "/detail",
		component: detail
	},{
		path:"/",
		redirect:"/index"
	}]
})

new Vue({
	el: "#demo",
	template:"<router-view></router-view>",
	router,
})



